@media (max-width: 1430px) {
	.footer {
		&_center {
			width: 100%;
			margin: 20px 0; }
		&_left, &_right {
			width: 100%; }
		&_app {
			justify-content: center; }
		&_social {
			justify-content: center; } } }


@media (max-width: 1199.98px) {
	.header {
		&_item {
			padding-right: 22px; }
		&_link {
			font-size: 14px; } }

	.invite {
		&_title {
			font-size: 48px;
			span {
				font-size: 32px; } } }

	.reason {
		&_about {
			max-width: 225px; }
		&_item {
			margin-top: 30px; }
		&_img {
			margin-right: 0; } }

	.mobile {
		&_item {
			margin-left: auto;
			margin-right: auto; } } }

@media (max-width: 991.98px) {
	.header {
		&_link {
			font-size: 12px; }
		&_item {
			padding-right: 12px; }
		&_dev {
			font-size: 11px; }
		&_phone {
			font-size: 18px; } }

	.invite {
		&_title {
			font-size: 40px;
			span {
				font-size: 24px; } } }

	.reason {
		&_item {
			flex-direction: column;
			margin-left: auto;
			margin-right: auto;
			align-items: center;
			text-align: center; }
		&_img {
			margin-bottom: 15px; }
		&_about {
			max-width: 100%;
			&_title {
				margin-bottom: 10px; }
			&_text {
				text-align: center; } } } }

@media (min-width: 768px) and (max-width: 840px) {
	.header {
		&_item {
			padding-right: 8px; } } }

@media (max-width: 767.98px) {
	.header {
		&_ul {
			height: 100vh;
			background-color: rgba(0,0,0,0.9);
			transition: all 0.7s;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			position: fixed;
			width: 70%;
			left: -100%;
			top: 0;
			&_active {
				left: 0; } }
		&_item {
			border: none;
			margin: 15px 0;
			padding-right: 0;
			text-align: center; }
		&_link {
			font-size: 17px;
			text-decoration: none;
			&:hover {
				color: #fea100;
				text-decoration: none; } }
		&_menu {
			&_btn {
				display: block;
				position: absolute;
				right: 10px;
				top: 50%;
				transform: translateY(-50%);
				width: 27px;
				background-color: transparent;
				border: none;
				height: 12px;
				span {
					display: block;
					height: 1px;
					width: 100%;
					background-color: #fff;
					margin-bottom: 3px;
					transition: all 0.4s; }
				&_active {
					span {
						margin-bottom: -1px;
						&:nth-child(1) {
							transform: translateY(-1px) rotate(-45deg); }
						&:nth-child(2) {
							display: none; }
						&:nth-child(3) {
							transform: translateY(-1px) rotate(45deg); } } } } }
		.hidden-xs {
			display: none; }
		.visible-xs {
			display: block; }

		&_phone {
			&_mini {
				margin-top: 15px;
				font-size: 14px;
				text-align: right; } }
		&_btn {
			&_mini {
				padding: 5px 0;
				margin: 0;
				margin-top: 10px;
				width: 128px;
				display: block;
				font-size: 12px;
				margin-left: auto; } } }
	.invite {
		&_title {
			font-size: 32px;
			line-height: 1.4;
			span {
				font-size: 18px; } }
		&_info {
			font-size: 16px; } } }

@media (max-width: 575.98px) {
	.header {
		&_dev {
			display: none; }
		&_logo {
			margin-top: 30px; } }
	.invite {
		min-height: 320px;
		&_info {
			display: none; }
		&_title {
			line-height: 1.5;
			span {
				font-size: 20px; } }
		&_btn {
			margin-top: 50px;
			padding: 15px 0;
			width: 220px; } }

	.reason {
		padding-top: 30px;
		padding-bottom: 10px;
		&_title {
			font-size: 24px;
			margin-bottom: 15px; }
		&_small {
			margin-bottom: 15px; }
		&_item {
			margin-top: 20px; }
		&_about {
			&_title {
				font-size: 16px; } } }

	.mobile {
		padding-top: 30px;
		&_item {
			max-width: 320px;
			width: 100%; } }

	.choose {
		padding-bottom: 0;
		padding-top: 0;
		min-height: 500px;
		background-color: #fff;
		&_text {
			display: none; } }

	.request {
		padding: 30px 0;
		padding-bottom: 0;
		&_item {
			margin-bottom: 20px; } }

	.world {
		padding-top: 20px;
		padding-bottom: 25px;
		background-size: cover;
		&_subtitle {
			padding-bottom: 180px; } }

	.footer {
		&_menu {
			flex-wrap: wrap;
			justify-content: space-between;
			li {
				a {
					font-size: 14px;
					font-weight: 300;
					margin-right: 0; } } }
		&_submenu {
			flex-wrap: wrap;
			justify-content: space-between;
			li {
				a {
					margin-right: 0;
					line-height: 2.4;
					&:first-child {
						margin-right: 20px; } } } } } }

