$bgdark: #212121;
$blue: #1eacc7;


body {
	font-family: 'Roboto', sans-serif; }

img {
	display: block;
	max-width: 100%;
	height: auto; }

button, button:focus {
	outline: none; }

.header {
	position: relative;
	.visible-xs {
		display: none; }
	&_nav {
		padding: 13px 70px;
		height: 50px;
		background-color: $bgdark;
		position: fixed;
		z-index: 20;
		width: 100%; }
	&_menu {
		&_btn {
			display: none; } }
	&_ul {
		list-style-type: none;
		padding: 0;
		margin: 0;
		display: flex;
		justify-content: space-between; }
	&_link {
		font-size: 15px;
		font-weight: 300;
		color: #fff;
		text-decoration: none;
		&:hover {
			text-decoration: none;
			color: #af8; } }
	&_item {
		border-right: 1px dotted #000;
		padding-right: 35px;
		&:last-child {
			border-right: none; } }
	&_bottom {
		position: absolute;
		z-index: 4;
		width: 100%;
		height: 84px;
		background-color: rgba(0,0,0,0.6);
		top: 50px; }
	&_logo {
		max-width: 100%;
		display: block;
		margin-top: 16px; }
	&_dev {
		color: #fff;
		font-size: 13px;
		opacity: 0.7;
		margin: 0;
		margin-top: 2px; }
	&_cta {
		text-align: right;
		font-weight: 300;
		font-size: 12px;
		padding-top: 20px;
		color: #fff;
		letter-spacing: 0.7px;
		margin: 0;
		line-height: 1; }
	&_phone {
		font-size: 22px;
		color: #fff;
		margin: 0;
		display: block;
		line-height: 1;
		margin-top: 8px;
		text-align: right; }

	&_btn {
		width: 100%;
		border: none;
		border-radius: 4px;
		padding: 12px 0;
		background-color: none;
		font-size: 14px;
		text-transform: uppercase;
		color: #fff;
		letter-spacing: 0.4px;
		text-shadow: 1px 1px 0 rgba(0,0,0,0.8);
		background-image: linear-gradient(to top, $blue 0%, #4ce2ff 100%);
		margin-top: 19px;
		transition: color 0.4s;
		&:hover {
			color: #fea100; } } }

.invite {
	background: url('../img/main.jpg') center/cover no-repeat;
	position: relative;
	z-index: 2;
	color: #fff;
	text-align: center;
	padding-top: calc(70px + 134px);
	padding-bottom: 125px;
	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(#111, 0.55);
		z-index: -1; }
	&_title {
		font-size: 36px;
		font-weight: 900;
		font-size: 60px;
		text-transform: uppercase;
		margin: 0;
		line-height: 1;
		span {
			font-size: 36px;
			font-weight: 700;
			text-transform: uppercase;
			display: block;
			margin-top: 15px;
			margin-bottom: 25px; } }
	&_info {
		font-size: 18px;
		font-weight: 300;
		line-height: 1.6;
		margin: 0;
		margin-top: 23px; }
	&_btn {
		@extend .header_btn;
		width: 255px;
		padding: 22px 0;
		font-size: 18px;
		margin-top: 100px; } }

.reason {
	padding-top: 70px;
	padding-bottom: 35px;
	color: #222;
	&_small {
		color: #fff;
		font-weight: 300;
		font-size: 11px;
		padding: 8px 0;
		background-color: #000;
		text-align: center;
		text-transform: uppercase;
		display: block;
		width: 112px;
		margin: auto;
		margin-bottom: 35px;
		letter-spacing: 0.4px; }
	&_title {
		margin: 0;
		text-transform: uppercase;
		text-align: center;
		font-size: 38px;
		font-weight: 900;
		letter-spacing: 1px; }
	&_img {
		width: 120px;
		height: 120px;
		background-color: $blue;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-right: 25px; }
	&_item {
		display: flex;
		justify-content: space-between;
		padding: 0 35px;
		margin: 35px 0; }
	&_about {
		max-width: 345px;
		&_title {
			font-size: 17px;
			color: $blue;
			line-height: 1.5;
			font-weight: 400;
			margin: 0; }
		&_text {
			font-size: 15px;
			text-align: justify;
			margin: 0;
			line-height: 1.4; } } }

.mobile {
	padding-top: 50px;
	padding-bottom: 55px;
	background: url('../img/mobile_bg.jpg') center/cover no-repeat;
	text-align: center;
	&_title {
		@extend .reason_title; }
	&_small {
		@extend .reason_small; }
	&_subtitle {
		margin-bottom: 30px;
		font-size: 17px;
		letter-spacing: 0.4px;
		color: #112;
		display: block; }
	&_item {
		width: 320px;
		height: 280px;
		padding-top: 80px;
		margin-bottom: 25px;
		position: relative;
		&_1 {
			background: url('../img/mob_item_1.jpg') center/cover no-repeat; }
		&_2 {
			background: url('../img/mob_item_2.jpg') center/cover no-repeat; }
		&_3 {
			background: url('../img/mob_item_3.jpg') center/cover no-repeat; }
		&_4 {
			background: url('../img/mob_item_4.jpg') center/cover no-repeat; }
		&_5 {
			background: url('../img/mob_item_5.jpg') center/cover no-repeat; }
		&_6 {
			background: url('../img/mob_item_6.jpg') center/cover no-repeat; }

		&_title {
			font-size: 24px;
			font-weight: 400;
			text-transform: uppercase;
			color: #fff;
			margin: 0;
			margin-bottom: 100px; }
		&_img {
			width: 24px;
			height: 24px;
			padding: 5px;
			display: flex;
			justify-content: center;
			align-items: center;
			background-color: #000;
			border-radius: 50%;
			position: absolute;
			left: 50%;
			bottom: 35px;
			transform: translateX(-50%); } } }

.choose {
	text-align: center;
	padding-bottom: 100px;
	padding-top: 55px;
	position: relative;
	background: url('../img/mobile_bg.jpg') center/cover no-repeat;
	z-index: 2;
	.black {
		background-color: #070716;
		min-height: 560px;
		position: absolute;
		z-index: -1;
		width: 100%;
		top: -55px; }
	&_small {
		@extend .reason_small;
		background-color: #fff;
		color: #000; }
	&_title {
		@extend .reason_title;
		color: #fff; }
	&_subtitle {
		@extend .mobile_subtitle;
		color: #fff; }
	&_img {
		img {
			margin: auto;
			margin-top: 50px; } }
	&_text {
		margin: 0;
		margin-top: 30px;
		font-size: 17px;
		font-weight: 300; } }


.request {
	padding-top: 70px;
	padding-bottom: 5px;
	&_small {
		@extend .reason_small; }
	&_title {
		@extend .reason_title;
		margin-bottom: 30px; }
	&_item {
		margin-bottom: 80px;
		&_red {
			.request_item_title {
				color: #ff5656; }
			.request_item_list {
				li {
					&::after {
						background: url('../img/access_red.png'); } } } }
		&_list {
			padding: 0;
			margin: 0;
			margin-left: 20px;
			list-style-type: none;
			li {
				font-size: 17px;
				padding-left: 25px;
				margin-bottom: 10px;
				font-weight: 300;
				position: relative;
				&::after {
					content: '';
					position: absolute;
					width: 21px;
					height: 20px;
					background: url('../img/access.png');
					left: -10px;
					top: 3px; } } }
		&_title {
			font-size: 22px;
			color: $blue;
			font-weight: 400;
			margin: 0;
			margin-bottom: 10px;
			span {
				text-transform: uppercase; } }
		&_money {
			font-size: 14px;
			color: $blue;
			margin: 0;
			margin-top: 20px;
			font-weight: 900; } } }


.world {
	padding: 55px 0px 45px;
	position: relative;
	background: #000 url('../img/world_bg.jpg') center no-repeat;
	color: #fff;
	text-align: center;
	&_small {
		@extend .choose_small; }
	&_title {
		@extend .choose_title; }
	&_subtitle {
		@extend .mobile_subtitle;
		color: #fff;
		margin-bottom: 0;
		padding-bottom: 480px; }
	&_all {
		text-transform: uppercase;
		color: $blue;
		font-size: 15px;
		text-decoration: none;
		letter-spacing: 0.7px;
		transition: color 0.4s;
		&:hover {
			color: #fea100;
			text-decoration: none; } } }

.footer {
	padding-top: 12px;
	padding-bottom: 35px;
	position: relative;
	&::before {
		margin: auto;
		content: '';
		width: 300px;
		height: 5px;
		background-color: $blue;
		display: block;
		margin-bottom: 30px; }
	&_container {
		padding: 0 25px; }
	&_left, &_center, &_right {
		width: 33%;
		display: inline-block;
		vertical-align: top; }
	&_social {
		display: flex;
		list-style-type: none;
		padding: 0;
		margin: 0;
		li {
			&:last-child a {
				margin-right: 0; }
			a {
				display: flex;
				align-items: center;
				justify-content: center;
				width: 35px;
				height: 35px;
				background-color: #000;
				border-radius: 50%;
				margin-right: 10px;
				color: #fff;
				text-decoration: none;
				font-size: 18px;
				&:hover {
					color: #fea100; } } } }
	&_menu {
		display: flex;
		justify-content: center;
		list-style-type: none;
		padding: 0;
		margin: 0;
		margin-bottom: 15px;
		li {
			&:last-child a {
				margin-right: 0; }
			a {
				text-transform: uppercase;
				color: #111;
				font-weight: 400;
				font-size: 15px;
				display: block;
				margin-right: 40px;
				text-decoration: none; } } }
	&_submenu {
		padding: 0;
		margin: 0;
		list-style-type: none;
		display: flex;
		justify-content: center;
		margin-bottom: 20px;
		li {
			&:last-child a {
				margin-right: 0; }
			a {
				display: block;
				margin-right: 30px;
				color: #999;
				font-weight: 300;
				text-transform: uppercase;
				font-size: 12px;
				text-decoration: none; } } }
	&_lang {
		display: block;
		text-align: center;
		text-transform: uppercase;
		font-size: 12px;
		color: $blue;
		text-align: center;
		text-decoration: none;
		&:hover {
			color: #fea100;
			text-decoration: none; } }
	&_app {
		padding: 0;
		margin: 0;
		list-style-type: none;
		display: flex;
		justify-content: flex-end;
		&_1 {
			background: #000 url('../img/footer_app.png') center/cover no-repeat; }
		&_2 {
			background: #000 url('../img/footer_app.png') center/cover no-repeat;
			background-position: 0px 0px; }
		&_3 {
			background: #000 url('../img/footer_app.png') center/cover no-repeat;
			background-position: -200px 0px; }
		li {
			&:last-child a {
				margin-right: 0; }
			a {
				display: block;
				width: 95px;
				height: 27px;
				background-color: #fff;
				margin-right: 5px; } } } }


@import 'media';


